/* App.css */
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 20px; /* Adiciona algum espaço ao redor do conteúdo */
  box-sizing: border-box; /* Inclui o padding e border na largura e altura total */
}

main {
  padding: 20px;
  max-width: 1200px; /* Ajusta a largura máxima */
  margin: 0 auto; /* Centraliza o conteúdo */
  overflow-x: hidden; /* Evita rolagem horizontal */
}

/* Ajusta o layout para melhor visualização em dispositivos móveis */
@media (max-width: 768px) {
  main {
    padding: 10px;
  }

  input, select, button {
    width: 100%;
    box-sizing: border-box;
  }

  .container {
    width: 100%;
    padding: 0 10px;
  }
}
