/* RegisterService.css */
.register-service-container {
    margin: 20px auto;
    padding: 20px;
    max-width: 600px; /* Ajusta a largura máxima */
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
  }

  .register-service-container h2 {
    margin-bottom: 20px; /* Adiciona um espaço abaixo do título */
  }

  
  .form-select,
  .form-input,
  .form-button {
    display: block;
    width: 100%;
    margin: 10px 0;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .form-button {
    background-color: #007bff;
    color: white;
    cursor: pointer;
  }
  
  .form-button:hover {
    background-color: #0056b3;
  }
  